* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background: #0b2434;
    padding: 20px;
}


main {
    background: #f5f5f5;
    height: 400px;
    max-width: 800px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* margin: 23vh auto; */
    margin: 0 auto;
}

@media (max-width:370px) {
    main {
        width: 100%;
        height: 100%;
    }
}

.die-container {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
}


@media (max-width:425px) {
    .die-container {
        display: flex;
        flex-wrap: wrap;
    }
}

@media (max-width:409px) {
    .die-container {
        align-items: center;
        justify-content: center;
    }
}


.die-face {
    height: 50px;
    width: 50px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.die-num {
    font-size: 2rem;
    user-select: none;
}

button {
    margin-top: 20px;
    padding: 10px 30px;
    border: 1px solid #5035ff;
    background: #5035ff;
    color: white;
    cursor: pointer;
    border-radius: 6px;
    font-size: 1rem;
}

button:focus {
    outline: none;
}

button:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.title {
    font-size: 40px;
    margin: 0;
}


.instuctions {
    font-weight: 400;
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: center;
    width: 300px;
}

@media (max-width:370px) {
    .instuctions {
        width: 100%;
    }
}